@import './modules/buttons.scss';
@import './modules/box.scss';
@import './fonts';

.store_main_container {
    position: absolute;
    width: 100vw;
    height: 150vw;
    padding-top: 5vw;
    background-color: rgb(250, 250, 250);
}
// --------------- categorie btns ----------------
.main_menu_btn {
    float: left;
    @include customFont(Lato1, 5vw);
    @include menu_btn(90vw, 20vw, 5vw, 2vw);
    overflow: hidden;
}
.button_bg {
    position: relative;
    width: 100%;
    height: 30vw;
    margin-top: -30vw;
    background: no-repeat right url(../images/icons/serifmakit.svg);
    opacity: 0.15;
    filter: invert(8%) sepia(96%) saturate(5586%) hue-rotate(13deg) brightness(100%) contrast(103%);
}
// ----------------- product buttons ------------------
.secondary_menu_btn {
    @include flexbox();
    float: left;
    @include customFont(Lato1, 5vw);
    @include menu_btn(90vw, 50vw, 5vw, 2vw);
    overflow: hidden;
}
.secondary_btn_info_container {
    @include flexbox(row);
    width: 86vw;
    height: 10vw;
    margin-left: 2vw;
    margin-right: 2vw;
    margin-top: 3vw;
}
.secondary_btn_info_name {
    width:20vw;
    height: 10vw;
    @include customFont(Lato1, 4vw);
    padding-right: 1vw;
    padding-left: 1vw;
}
.secondary_btn_info_description {
    width: 46vw;
    height: 10vw;
    @include customFont(Lato1, 3vw);
    padding-right: 1vw;
    padding-left: 1vw;
}
.secondary_btn_info_price {
    width: 20vw;
    height: 10vw;
    @include customFont(Lato1, 5vw);
    padding-right: 1vw;
    padding-left: 1vw;
}

