@mixin menu_btn($width, $height, $marginh, $marginv) {
 width: $width;
 height: $height;
 margin-left: $marginh;
 margin-right: $marginh;
 margin-top: $marginv;
 margin-bottom: $marginv;
 border-style: solid;
 border-width: 1px;
 border-radius: 2vw;
 background-color: white;
 border-color: rgb(225, 225, 225);
 box-shadow: 0.3vw 0.3vw 2vw 0.3vw rgba(0,0,0,0.1);
}